class Studios{

    static SetCurrentStudio(studio){
        localStorage.setItem("CurrentStudio", studio);
    }

    static GetCurrentStudio(){
        return localStorage.getItem("CurrentStudio");
    }

    static UpdateStudioJSON(json){
        let studio = this.GetCurrentStudio();
        localStorage.setItem(studio, json);
    }

    static GetStudioJSON(){
        let studio = this.GetCurrentStudio();
        return localStorage.getItem(studio);
    }

}

export default Studios


