import React, {Component} from 'react';
import Studios from '../Studios'

class TextStudio extends Component {

    constructor() {
        super();

        Studios.SetCurrentStudio("TextStudio");
    }

    render() {
        return (
            <h1>Text Studio</h1>
        )
    }

}

export default TextStudio


