import React, {Component} from 'react';
import Studios from '../Studios'

const titleStyle = {
    background: 'red',
    color: 'white'
};

class Container extends Component {

    constructor(props) {
        super(props);

        this.state = {
            title: "Hello, world!",
            style: {
                color: '#ffffff',
                background: '#cccccc'
            }
        };

        let studioJSON = Studios.GetStudioJSON();
        if(studioJSON !== null) this.state = JSON.parse(studioJSON);

        Studios.SetCurrentStudio("Container");

        this.handleInputChange = this.handleInputChange.bind(this);
        this.changeStyle = this.changeStyle.bind(this);

        localStorage.setItem("titleStyle", JSON.stringify(this.state.style));
    }

    handleInputChange(event) {
        const target = event.target;
        const value = target.type === 'checkbox' ? target.checked : target.value;
        const name = target.name;

        this.setState({
            [name]: value
        });
    }

    changeStyle(event){
        const target = event.target;
        const value = target.type === 'checkbox' ? target.checked : target.value;
        const name = target.name;

        let titleStyle = JSON.parse(localStorage.getItem("titleStyle"));
        titleStyle[name] = value;

        localStorage.setItem("titleStyle", JSON.stringify(titleStyle));

        this.setState({
            style: titleStyle
        });
    }

    render() {
        let output = JSON.stringify(this.state);
        Studios.UpdateStudioJSON(output);

        return (
            <div className="container-fluid">
                <div className="row">
                    <div className="col-4">
                        <div className="card">
                            <div className="card-body">
                                <h4 className="card-title">Container Studio</h4>
                                <div className="form-group">
                                    <label htmlFor="">Title</label>
                                    <input type="text" name="title" className="form-control" value={this.state.title} onChange={this.handleInputChange}/>
                                </div>
                                <div className="form-group">
                                    <label htmlFor="">Color</label>
                                    <input type="color" name="color" value={this.state.style.color} onChange={this.changeStyle}/>
                                </div>
                                <div className="form-group">
                                    <label htmlFor="">Background Color</label>
                                    <input type="color" name="background" value={this.state.style.background} onChange={this.changeStyle}/>
                                </div>
                            </div>
                            <div className="card-body">
                                <label htmlFor="">Output JSON</label>
                                <textarea cols="30" rows="10" className="form-control" value={output} readOnly/>
                            </div>
                        </div>
                    </div>
                    <div className="col-8">
                        <div className="jumbotron">
                            <h1 className="display-3" style={JSON.parse(localStorage.getItem("titleStyle"))}>{this.state.title}</h1>
                            <p className="lead">This is a simple hero unit, a simple jumbotron-style component for calling
                                extra attention to featured content or information.</p>
                            <hr className="my-4"/>
                            <p>It uses utility classes for typography and spacing to space content out within the larger
                                container.</p>
                            <p className="lead">
                                <a className="btn btn-primary btn-lg" role="button">Learn more</a>
                            </p>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default Container


