import React, { Component } from 'react';
import '../../styles/App.css'
import {
    Route,
    HashRouter
} from 'react-router-dom'

import Header from '../../components/Header'
import Home from '../../components/Home'
import Container from '../../components/Studios/Container'
import TextStudio from '../../components/Studios/Text'
import Image from '../../components/Studios/Image'

class App extends Component {
    constructor() {
        super();

        this.outputJSON = {};
    }

  render() {
    return (
        <HashRouter>
            <div>
                <Header></Header>

                <Route exact path="/" component={Home}/>
                <Route path="/container" component={Container}/>
                <Route path="/text" component={TextStudio}/>
                <Route path="/image" component={Image}/>
            </div>
        </HashRouter>
    );
  }
}

export default App;


