import React, {Component} from 'react';
import Studios from '../Studios'

class Image extends Component {

    constructor() {
        super();

        Studios.SetCurrentStudio("Image");
    }

    render() {
        return (
            <h1>Image Studio</h1>
        )
    }

}

export default Image


